<template>
   <Nav />
  <div class="container">
  <Main @toggle-add-task="toggleAddTask" title="Task Tracker" :showAddTask="showAddTask" />
  <div v-show="showAddTask">
  <AddTask @add-task="addTask" />
  </div>
  <Tasks @toggle-reminder="toggleReminder" @delete-task="deleteTask" :tasks="tasks" />
  
  </div>  
  
</template>

<script>

import Nav from './components/Nav'
import Main from './components/Main'
import Tasks from './components/Tasks'
import AddTask from './components/AddTask'

export default {
  name: 'App',
  components: {
    Nav, Main, Tasks, AddTask
  },
  data(){

      return{
        tasks: [],
        showAddTask: false

      }
  },
  methods:{
    toggleAddTask(){

      this.showAddTask = !this.showAddTask

    },

    addTask(task){
      this.tasks ={...this.tasks, task}


    },
    
    
    deleteTask(id){

      // console.log('task' ,id);

      if(confirm('Are you sure you want to delete?')){

      this.tasks = this.tasks.filter((task) => task.id !== id)
                    /* i-ffilter lang // magrremain yung mga items na hindi equal sa clinick mong id */ 
     }
    
    },

    toggleReminder(id){
      

      this.tasks = this.tasks.map((task) =>
      task.id === id ?
       {...task, reminder: !task.reminder} : task)
        //will reverse status ng reminder (true / false statement)
    },

  },

  created(){
    this.tasks =[
      {
        id: 1,
        text: 'Grocery',
        day: 'Sunday at 9am',
        reminder: true,
      },
       {
        id: 2,
        text: 'Haircut',
        day: 'Sunday at 1pm',
        reminder: false,
      },


    ]


  }


}


</script>

